nt-family: sans-serif;\n}\n#hero {\n    height: 80vh;\n    width: 100%;\n    display: flex;\n    flex-direction: row;\n    /* border: solid black 1px; */\n    align-self: center;\n}\n.left-sidebar {\n    display: flex;\n    flex-direction: column;\n    background-color: var(--third-color);\n    width: 28%;\n    height: 100vh;\n    transition: opacity 150ms ease-in-out, transform 150ms ease-in-out;\n}\n.left-sidebar.unactive {\n    transform: translateX(-10px);\n    opacity: 0;\n}\n.project-bar {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n}\n.project-header {\n    width: 80%;\n    font-size: 18px;\n    display: flex;\n    flex-direction: row;\n    justify-content: center;\n    align-items: center;\n    align-self: center;\n    margin-top: 24px;\n}\n.project-header-left {\n    display: flex;\n    flex-direction: row;\n    /* justify-content: center; */\n    align-items: center;\n    /* margin-left: 25px; */\n    gap: 10px;\n    cursor: pointer;\n    width: 100%;\n}\n.fa-angle-down {\n    transform: rotate(0deg);\n    transition: transform 150ms ease-in-out;\n}\n.fa-angle-down.active {\n    transform: rotate(-90deg);\n}\n#project-title {\n    font-weight: 600;\n}\n#project-addBtn {\n    font-family: sans-serif;\n    font-size: 1.8em;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    margin-top: 5px;\n    padding-bottom: 5px;\n    height: 25px;\n    width: 30px;\n    cursor: pointer;\n    border-radius: 10px;\n    margin-right: 9px;\n}\n#project-addBtn:hover {\n    background-color: var(--fourth-color);\n}\n.addProject-form {\n    width: 55%;\n    align-self: center;\n    /* margin-left: 30px; */\n    margin-right: 20px;\n    display: none;\n    border: solid lightgray 1px;\n}\n.addProject-form:focus {\n    outline: none;\n    /* border: solid lightgray 2px; */\n}\n.addProject-form.active {\n    display: flex;\n}\n.project-list {\n    /* border: solid black 1px; */\n    display: block;\n    font-weight: